'use client';
import { useState, useRef, useEffect } from 'react';

export default function Chat({ agentId, walletAddress, onSendMessage }) {
    const [messages, setMessages] = useState([]);
    const [input, setInput] = useState('');
    const [loading, setLoading] = useState(false);
    const messagesEndRef = useRef(null);

    const scrollToBottom = () => {
        messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
    };

    useEffect(() => {
        scrollToBottom();
    }, [messages]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        if (!input.trim() || !agentId || !walletAddress) return;

        const userMessage = input;
        setInput('');
        setLoading(true);

        // Add user message to UI immediately
        setMessages(prev => [...prev, { role: 'user', content: userMessage }]);

        try {
            const response = await onSendMessage(userMessage);
            setMessages(prev => [...prev, { role: 'agent', content: response }]);
        } catch (error) {
            console.error("Chat error:", error);
            setMessages(prev => [...prev, { role: 'system', content: "Error: Failed to get response." }]);
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="chat-container">
            <div className="messages-area">
                {messages.length === 0 && (
                    <div className="empty-state">
                        Start a conversation with your immutable agent.
                    </div>
                )}
                {messages.map((msg, idx) => (
                    <div key={idx} className={`message ${msg.role}`}>
                        <div className="message-content">{msg.content}</div>
                    </div>
                ))}
                {loading && <div className="message agent loading">Thinking...</div>}
                <div ref={messagesEndRef} />
            </div>

            <form onSubmit={handleSubmit} className="input-area">
                <input
                    type="text"
                    value={input}
                    onChange={(e) => setInput(e.target.value)}
                    placeholder={walletAddress ? "Type a message..." : "Connect wallet to chat"}
                    disabled={!walletAddress || loading}
                />
                <button type="submit" disabled={!walletAddress || loading}>
                    Send
                </button>
            </form>

            <style jsx>{`
        .chat-container {
          display: flex;
          flex-direction: column;
          height: 100%;
          border: 1px solid #333;
          border-radius: 8px;
          background: #111;
          overflow: hidden;
        }
        .messages-area {
          flex: 1;
          padding: 20px;
          overflow-y: auto;
          display: flex;
          flex-direction: column;
          gap: 15px;
        }
        .message {
          max-width: 80%;
          padding: 10px 15px;
          border-radius: 12px;
          line-height: 1.4;
        }
        .message.user {
          align-self: flex-end;
          background: #0070f3;
          color: white;
        }
        .message.agent {
          align-self: flex-start;
          background: #333;
          color: #eee;
        }
        .message.system {
          align-self: center;
          background: transparent;
          color: #ff4444;
          font-size: 0.9em;
        }
        .input-area {
          display: flex;
          padding: 15px;
          background: #1a1a1a;
          border-top: 1px solid #333;
        }
        input {
          flex: 1;
          padding: 10px;
          border-radius: 4px;
          border: 1px solid #333;
          background: #000;
          color: white;
          margin-right: 10px;
        }
        button {
          padding: 10px 20px;
          background: #0070f3;
          color: white;
          border: none;
          border-radius: 4px;
          cursor: pointer;
        }
        button:disabled {
          background: #444;
          cursor: not-allowed;
        }
        .empty-state {
            text-align: center;
            color: #666;
            margin-top: 50px;
        }
      `}</style>
        </div>
    );
}
