'use client';
import { useState, useEffect } from 'react';
import { ethers } from 'ethers';
import axios from 'axios';
import Chat from './components/Chat';

export default function Home() {
  const [walletAddress, setWalletAddress] = useState('');
  const [agent, setAgent] = useState(null);
  const [loading, setLoading] = useState(false);

  // Constants
  const BACKEND_URL = 'http://localhost:3001';
  // Mock Agent Data for Prototype if not on-chain yet
  const MOCK_AGENT = {
    id: 1,
    innovationScore: 5,
    memoryCount: 12
  };

  useEffect(() => {
    checkConnection();
  }, []);

  const checkConnection = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const provider = new ethers.BrowserProvider(window.ethereum);
        const accounts = await provider.listAccounts();
        if (accounts.length > 0) {
          setWalletAddress(accounts[0].address);
          fetchAgentData(1); // Default to Agent 1 for prototype
        }
      } catch (error) {
        console.error("Connection check failed", error);
      }
    }
  };

  const connectWallet = async () => {
    if (typeof window.ethereum !== 'undefined') {
      try {
        const provider = new ethers.BrowserProvider(window.ethereum);
        await provider.send("eth_requestAccounts", []);
        const signer = await provider.getSigner();
        setWalletAddress(await signer.getAddress());
        fetchAgentData(1);
      } catch (error) {
        console.error("Connection failed", error);
        alert("Failed to connect wallet.");
      }
    } else {
      alert("Please install MetaMask!");
    }
  };

  const fetchAgentData = async (agentId) => {
    setLoading(true);
    // In a real app, fetch from smart contract or backend
    // For prototype, we simulate fetching
    setTimeout(() => {
      setAgent(MOCK_AGENT);
      setLoading(false);
    }, 1000);
  };

  const handleSendMessage = async (message) => {
    try {
      const response = await axios.post(`${BACKEND_URL}/chat`, {
        message,
        agentId: agent.id,
        walletAddress
      });
      return response.data.reply;
    } catch (error) {
      console.error("API Error:", error);
      throw error;
    }
  };

  return (
    <main className="main-container">
      <header className="header">
        <h1 className="logo">CenceraAI</h1>
        <div className="wallet-section">
          {walletAddress ? (
            <span className="address">
              {walletAddress.slice(0, 6)}...{walletAddress.slice(-4)}
            </span>
          ) : (
            <button onClick={connectWallet} className="connect-btn">
              Connect Wallet
            </button>
          )}
        </div>
      </header>

      <div className="content">
        <div className="agent-card">
          <h2>Your Agent</h2>
          {loading ? (
            <p>Loading agent data...</p>
          ) : agent ? (
            <div className="stats">
              <div className="stat">
                <span className="label">ID</span>
                <span className="value">#{agent.id}</span>
              </div>
              <div className="stat">
                <span className="label">Innovation Score</span>
                <span className="value">{agent.innovationScore}</span>
              </div>
              <div className="stat">
                <span className="label">Memories</span>
                <span className="value">{agent.memoryCount}</span>
              </div>
            </div>
          ) : (
            <p>Connect wallet to view agent.</p>
          )}
        </div>

        <div className="chat-section">
          <Chat
            agentId={agent?.id}
            walletAddress={walletAddress}
            onSendMessage={handleSendMessage}
          />
        </div>
      </div>

      <style jsx global>{`
        body {
          margin: 0;
          font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen,
            Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
          background: #000;
          color: #fff;
        }
      `}</style>

      <style jsx>{`
        .main-container {
          max-width: 1200px;
          margin: 0 auto;
          padding: 20px;
          height: 100vh;
          display: flex;
          flex-direction: column;
        }
        .header {
          display: flex;
          justify-content: space-between;
          align-items: center;
          padding-bottom: 20px;
          border-bottom: 1px solid #333;
        }
        .logo {
          font-size: 1.5rem;
          font-weight: bold;
          background: linear-gradient(45deg, #0070f3, #00ff88);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
        }
        .connect-btn {
          background: white;
          color: black;
          border: none;
          padding: 10px 20px;
          border-radius: 20px;
          cursor: pointer;
          font-weight: bold;
          transition: transform 0.2s;
        }
        .connect-btn:hover {
          transform: scale(1.05);
        }
        .address {
          font-family: monospace;
          background: #222;
          padding: 8px 12px;
          border-radius: 12px;
        }
        .content {
          display: flex;
          gap: 20px;
          flex: 1;
          margin-top: 20px;
          overflow: hidden;
        }
        .agent-card {
          flex: 1;
          background: #111;
          border: 1px solid #333;
          border-radius: 12px;
          padding: 20px;
          max-width: 300px;
        }
        .stats {
          display: flex;
          flex-direction: column;
          gap: 15px;
          margin-top: 20px;
        }
        .stat {
          display: flex;
          justify-content: space-between;
          padding: 10px;
          background: #222;
          border-radius: 8px;
        }
        .label {
          color: #888;
        }
        .value {
          font-weight: bold;
          color: #00ff88;
        }
        .chat-section {
          flex: 2;
          display: flex;
          flex-direction: column;
        }
      `}</style>
    </main>
  );
}
